<template>
  <div class="content">
    <el-form
      :inline="true"
      ref="searchForm"
      :model="searchFormData"
      :rules="searchFormRule"
      status-icon
      hide-required-asterisk
      class="search-form"
    >
      <el-form-item label="" prop="title">
        <el-input v-model="searchFormData.title" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitSearch" :loading="searching">
          
        </el-button>
        <el-button type="primary" @click="loadQuestions">
          
        </el-button>
        <el-button type="primary" @click="handleCreateQuestion">
          
        </el-button>
      </el-form-item>
    </el-form>
    <div class="questions-table">
      <el-table :data="questions" border>
        <el-table-column
          prop="id"
          label="ID"
          min-width="50"
        ></el-table-column>
        <el-table-column label="" min-width="50">
          <template slot-scope="scope">
            {{ questionType(scope.row.flag) }}
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
          min-width="200"
        ></el-table-column>
        <el-table-column label="" min-width="200">
          <template slot-scope="scope">
            {{ questionsOptions(scope.row.options) }}
          </template>
        </el-table-column>
        <el-table-column
          prop="answer"
          label=""
          min-width="100"
        ></el-table-column>
        <el-table-column label="" fixed="right" min-width="100">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      @size-change="handlePageChange"
      @current-change="handlePageChange"
      :current-page.sync="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size.sync="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalPage"
      background
      class="questions-pagination"
    >
    </el-pagination>
    <el-dialog title="" :visible.sync="createDialogEdit">
      <el-form
        ref="createForm"
        :model="createFormData"
        :rules="createFormRule"
        status-icon
        hide-required-asterisk
        label-position="left"
        label-width="20%"
      >
        <el-form-item prop="title" label="">
          <el-input
            type="text"
            v-model="createFormData.title"
            placeholder=""
          />
        </el-form-item>
        <el-table :data="createFormData.options" style="width: 100%">
          <el-table-column
            label=""
            type="index"
            :index="index => String.fromCharCode(65 + index)"
            min-width="100"
          ></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-input v-model="scope.row.label"></el-input>
            </template>
          </el-table-column>
          <el-table-column align="right">
            <template slot="header">
              <el-button size="mini" @click="handleCreateOption()">
                
              </el-button>
            </template>
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.isRight"
                active-color="#13ce66"
                inactive-color="#ff4949"
              >
              </el-switch>
              <el-button
                size="mini"
                type="danger"
                @click="handleDeleteOption(scope.$index, scope.row)"
              >
                Delete
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="createDialogEdit = false">
           
        </el-button>
        <el-button type="primary" @click="submitCreate()" :loading="editing">
          
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "back-questions",
  data() {
    return {
      currentPage: 1,
      totalPage: 1,
      pageSize: 10,
      createDialogEdit: false,
      editDialogEdit: false,
      editing: false,
      questions: [],
      searching: false,
      searchFormData: {
        title: ""
      },
      searchFormRule: {
        title: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      createFormData: {
        title: "",
        options: []
      },
      createFormRule: {}
    };
  },
  methods: {
    ...mapActions("admin_questions", [
      "getQuestions",
      "deleteQuestion",
      "searchQuestion",
      "createQuestion"
    ]),
    questionType(flag) {
      if (flag === "0") {
        return "";
      }
      if (flag === "1") {
        return "";
      }
    },
    questionsOptions(options) {
      options = JSON.parse(options.replace(/'/g, '"'));
      let result = [];
      for (let i = 0; i < options.length; i++) {
        result.push(String.fromCharCode(65 + i) + ": " + options[i]);
      }
      return result.join(" | ");
    },
    handleDelete(index, question) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.deleteQuestion(question.id)
            .then(res => {
              this.$delete(
                this.questions,
                this.questions.findIndex(item => item.id === question.id)
              );
              this.$notify.success({
                title: "",
                message: ""
              });
            })
            .catch(err => {
              this.$notify.error({
                title: "",
                message: err.data.error
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    submitCreate() {
      this.$refs.createForm.validate(valid => {
        if (valid) {
          this.editing = true;
          let data = { ...this.createFormData };
          let options = [];
          let answer = "";
          for (let i = 0; i < data.options.length; i++) {
            options.push(data.options[i].label);
            if (data.options[i].isRight) {
              answer += String.fromCharCode(65 + i);
            }
          }
          data.options = options;
          data.answer = answer;
          this.createQuestion(data)
            .then(res => {
              this.editing = false;
              this.createDialogEdit = false;
              // add to view
              this.loadQuestions();
              this.$notify.success({
                title: "",
                message: ""
              });
            })
            .catch(err => {
              this.editing = false;
              this.$notify.error({
                title: "",
                message: err.data.error
              });
            });
        }
      });
    },
    handlePageChange(size) {
      this.getQuestions({
        page: this.currentPage,
        pre_page: this.pageSize
      })
        .then(res => {
          this.questions = res.data.data.questions;
          this.totalPage = res.data.data.count;
        })
        .catch(err => {
          this.$notify.error({
            title: "",
            message: err.data.error
          });
        });
    },
    submitSearch() {
      this.$refs.searchForm.validate(valid => {
        if (valid) {
          this.searchQuestion(this.searchFormData)
            .then(res => {
              this.questions = res.data.data;
              this.totalPage = this.questions.length;
              this.pageSize = this.questions.length;
            })
            .catch(err => {
              this.$notify.error({
                title: "",
                message: err.data.error
              });
            });
        }
      });
    },
    loadQuestions() {
      this.searchFormData.title = "";
      this.pageSize = 10;
      this.currentPage = 1;
      this.getQuestions({
        page: 1,
        pre_page: 10
      })
        .then(res => {
          this.questions = res.data.data.questions;
          this.totalPage = res.data.data.count;
        })
        .catch(err => {
          this.$notify.error({
            title: "",
            message: err.data.error
          });
        });
    },
    handleCreateQuestion() {
      this.createDialogEdit = true;
      this.$set(this, "createFormData", {
        title: "",
        options: []
      });
    },
    handleCreateOption() {
      this.createFormData.options.push({
        label: "",
        isRight: false
      });
    },
    handleDeleteOption(index, option) {
      this.$delete(this.createFormData.options, index);
    }
  },
  mounted() {
    this.loadQuestions();
  }
};
</script>

<style lang="scss" scoped>
.search-form {
  margin-top: 22px;
  margin-left: 1rem;
}

.questions-table {
  margin-left: 1rem;
  margin-right: 1rem;
}

.questions-pagination {
  margin: 22px 1rem;
}

.el-table {
  .el-switch {
    margin-right: 1rem;
  }
}
</style>
